<template>
  <div class="container">
    <h2>
      <i18n path="common.install"></i18n>
    </h2>
    <hr class="my-2" />
    <p>
      <i18n path="common.guide.useNpm"></i18n>
    </p>
    <showcode :trigger="false">
      <pre class="brush:js">
      npm i -S v-region
      </pre>
    </showcode>

    <p>
      <i18n path="common.guide.include"></i18n>
    </p>
    <showcode :trigger="false">
      <pre class="brush:js">
      import Vue from 'vue'
      import vRegion from 'v-region'
      Vue.use(vRegion, { global config options... })
      </pre>
    </showcode>

    <!--<div class="panel panel-default">-->
    <!--<div class="panel-body">-->
    <!--<p><i18n path="common.globalConfig"></i18n></p>-->
    <!--<ul>-->
    <!--<li>i18n</li>-->
    <!--<li>blank</li>-->
    <!--<li>town</li>-->
    <!--<li>search</li>-->
    <!--</ul>-->
    <!--</div>-->
    <!--</div>-->

    <h2 class="mt-5">
      <i18n path="common.deploy"></i18n>
    </h2>
    <hr class="my-2" />
    <showcode :trigger="false">
      <script type="syntaxhighlighter" class="brush:html">
      <![CDATA[
      <template>
        <v-region @values="regionChange"></v-region>
      </template>

      &lt;script&gt;
      export default {
        methods: {
          //receive selected region data
          regionChange (data) {
            console.log(data)
          }
        }
      }
      &lt;/script&gt;
      ]]>
      </script>
    </showcode>

    <h2 class="mt-5">
      <i18n path="common.example"></i18n>
      <small class="ml-3">
        <!--<i18n path="region.demo.desc"></i18n>-->
        <a href="https://codepen.io/terry05/pen/ERNvzJ" target="_blank">
          <i18n path="common.codePen"></i18n>
        </a>
      </small>
    </h2>
    <hr class="my-2" />
    <h3 class="mt-5">
      <i18n path="region.demo.modes.formElement"></i18n>
    </h3>
    <div class="card p-5 shadow-sm">
      <h4>
        <i18n path="region.demo.province"></i18n>
      </h4>
      <v-region :city="false" :area="false"></v-region>
      <showcode>
        <script type="syntaxhighlighter" class="brush:js;">
        <![CDATA[
        <v-region :city="false" :area="false"></v-region>
        ]]>
        </script>
      </showcode>
      <br />

      <h4>
        <i18n path="region.demo.province"></i18n>、
        <i18n path="region.demo.city"></i18n>
      </h4>
      <v-region :area="false"></v-region>
      <showcode>
        <script type="syntaxhighlighter" class="brush:js;">
        <![CDATA[
        <v-region :area="false"></v-region>
        ]]>
        </script>
      </showcode>
      <br />

      <h4>
        <i18n path="region.demo.province"></i18n>、
        <i18n path="region.demo.city"></i18n>、
        <i18n path="region.demo.area"></i18n>
      </h4>
      <v-region></v-region>
      <showcode>
        <script type="syntaxhighlighter" class="brush:js;">
        <![CDATA[
        <v-region></v-region>
        ]]>
        </script>
      </showcode>
      <br />

      <h4>
        <i18n path="region.demo.province"></i18n>、
        <i18n path="region.demo.city"></i18n>、
        <i18n path="region.demo.area"></i18n>、
        <i18n path="region.demo.town"></i18n>
      </h4>
      <p>
        <i18n path="region.demo.selected"></i18n>:
      </p>
      <pre class="p-3 bg-light">{{JSON.stringify(selectResult, null, 4)}}</pre>
      <v-region :town="true" @values="selectLogs"></v-region>
      <showcode>
        <script type="syntaxhighlighter" class="brush:js;">
        <![CDATA[
        <v-region :town="true" @values="selectLogs"></v-region>
        ]]>
        </script>
      </showcode>
      <br />

      <h4>
        <i18n path="region.demo.init"></i18n>
      </h4>
      <v-region :town="true" v-model="selected"></v-region>
      <showcode>
        <script type="syntaxhighlighter" class="brush:js;">
        <![CDATA[
        <v-region :town="true" v-model="selected"></v-region>

        //script
        data(){
          return {
            selected: {
              province: '350000',
              city: '350100',
              area: '350104',
              town: '350104008'
            }
          }
        }
        ]]>
        </script>
      </showcode>
      <br />

      <h4>
        <i18n path="region.demo.disabled"></i18n>
      </h4>
      <v-region :town="true" :disabled="true" v-model="selected"></v-region>
      <showcode>
        <script type="syntaxhighlighter" class="brush:js;">
        <![CDATA[
        <v-region :town="true" :disabled="true" v-model="selected">
        </v-region>
        ]]>
        </script>
      </showcode>
      <br />
    </div>

    <h3 class="mt-5">
      <i18n path="region.demo.modes.plainText"></i18n>
    </h3>
    <div class="card p-5 shadow-sm">
      <h4>
        <i18n path="region.demo.text"></i18n>
      </h4>
      <p class="alert alert-secondary">
        <v-region type="text" :town="true" v-model="selected"></v-region>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:js;">
        <![CDATA[
        <v-region type="text" :town="true" v-model="selected">
        </v-region>

        //script
        data(){
          return {
            selected: {
              province: '350000',
              city: '350100',
              area: '350104',
              town: '350104008'
            }
          }
        }
        ]]>
        </script>
      </showcode>
      <br />
    </div>

    <h3 class="mt-5">
      <i18n path="region.demo.modes.selector"></i18n>
    </h3>
    <div class="card p-5 shadow-sm">
      <h4>
        <i18n path="region.demo.dropdown"></i18n>
      </h4>
      <p>
        <v-region type="group" :town="true" @values="regionChange">
        </v-region>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <!-- Use default region toggle button -->
        <v-region type="group" :town="true">
        </v-region>
        ]]>
        </script>
      </showcode>
      <br />

      <h4>
        <i18n path="region.demo.customCaller"></i18n>
        <small></small>
      </h4>
      <p>
        <v-region type="group" :town="true" @values="regionChange">
          <template #default="{ region, show }">
            <button type="button" class="btn btn-primary">
              region:{{resultText(region)}},
              show: {{show}}
            </button>
          </template>
        </v-region>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <!-- Customize region toggle -->
        <v-region type="group" :town="true" @values="regionChange">
          <template #default="{ region, show }">
            <button type="button" class="btn btn-primary">
              region:<span v-text="resultText(region)"></span>,
              show: <span v-text="show"></span>
            </button>
          </template>
        </v-region>
        ]]>
        </script>
      </showcode>
      <br />

      <h4>
        <i18n path="region.demo.column"></i18n>
      </h4>
      <p>
        <v-region type="column" :town="true"></v-region>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <!-- Use default region toggle button -->
        <v-region type="column" :town="true">
        </v-region>
        ]]>
        </script>
      </showcode>
      <br />

      <h4>
        <i18n path="region.demo.cityPicker"></i18n>
      </h4>
      <p>
        <v-region type="city" v-model="selectedCity" @values="citySelectedLogs">
        </v-region>
      </p>
      <pre class="p-3 bg-light mt-3">{{JSON.stringify(selectedCityResult, null, 4)}}</pre>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <v-region type="city" v-model="selected" @values="values">
        </v-region>

        //script
        export default {
          data(){
            return {
              //take city codes to make the corresponding city items selected
              selected: ['110000', '130200', '140100']
            }
          },
          methods: {
            //item selected callback
            //data format: [{key: 'xxx', value: 'xxx'},...]
            values(datas){
              console.log(datas)
            }
          }
        }
        ]]>
        </script>
      </showcode>
      <br />
    </div>

    <h2 class="mt-5">
      <i18n path="common.options"></i18n>
    </h2>
    <table class="table table-striped option-table">
      <thead>
        <tr>
          <th>
            <i18n path="common.tbTitle.name"></i18n>
          </th>
          <th>
            <i18n path="common.tbTitle.type"></i18n>
          </th>
          <th>
            <i18n path="common.tbTitle.desc"></i18n>
          </th>
          <th>
            <i18n path="common.tbTitle.required"></i18n>
          </th>
          <th>
            <i18n path="common.tbTitle.default"></i18n>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>type</td>
          <td>string</td>
          <td>
            <i18n path="region.option.type"></i18n>
            <ul class="mt-3">
              <li>'select'(default)</li>
              <li>'text'</li>
              <li>'group'</li>
              <li>'column'</li>
              <li>'city'</li>
            </ul>
          </td>
          <td>true</td>
          <td>'select'</td>
        </tr>
        <tr>
          <td>v-model/value</td>
          <td>object|array</td>
          <td>
            <i18n path="region.option.selected"></i18n>
            <br />
            <ul>
              <li>
                <code>province</code> province code
              </li>
              <li>
                <code>city</code> city code
              </li>
              <li>
                <code>area</code> area code
              </li>
              <li>
                <code>town</code> town code
              </li>
            </ul>
            <p>
              <i18n path="region.option.cityPickerSelected"></i18n>
            </p>
            <code>['city code1', 'city code2', ...]</code>
            <br />
            <br />
            <p>
              <strong>
                <i18n path="common.example"></i18n>
              </strong>:
            </p>
            <p>for 'select', 'group', 'text', 'column' modes:</p>
            <code>
              <pre class="text-danger">
{
  province: '350000',
  city: '350100',
  area: '350103',
  town: '350103012'
}</pre>
            </code>
            <br />
            <p>for 'city' mode:</p>
            <code>['110000', '130200', '140100']</code>
          </td>
          <td>false</td>
          <td></td>
        </tr>
        <tr>
          <td>blank</td>
          <td>boolean</td>
          <td>
            <i18n path="region.option.blank"></i18n>
          </td>
          <td>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>disabled</td>
          <td>boolean</td>
          <td>
            <i18n path="region.option.disabled"></i18n>
          </td>
          <td>false</td>
          <td>false</td>
        </tr>
        <tr>
          <td>city</td>
          <td>boolean</td>
          <td>
            <i18n path="region.option.city"></i18n>
          </td>
          <td>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>area</td>
          <td>boolean</td>
          <td>
            <i18n path="region.option.area"></i18n>
          </td>
          <td>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>town</td>
          <td>boolean</td>
          <td>
            <i18n path="region.option.town"></i18n>
          </td>
          <td>false</td>
          <td>false</td>
        </tr>
        <tr>
          <td>search</td>
          <td>boolean</td>
          <td>
            <i18n path="region.option.search"></i18n>
          </td>
          <td>false</td>
          <td>true</td>
        </tr>
        <tr>
          <td>language</td>
          <td>string</td>
          <td>
            <i18n path="region.option.language"></i18n>
            <ul class="mt-3">
              <li><code class="mr-3">cn</code> <i18n path="common.lang.cn"></i18n></li>
              <li><code class="mr-3">en</code> <i18n path="common.lang.en"></i18n></li>
            </ul>
          </td>
          <td>false</td>
          <td>true</td>
        </tr>
      </tbody>
    </table>

    <h2 class="mt-5">
      <i18n path="common.events"></i18n>
    </h2>
    <table class="table">
      <thead>
        <tr>
          <th>
            <i18n path="common.tbTitle.param"></i18n>
          </th>
          <th>
            <i18n path="common.tbTitle.desc"></i18n>
          </th>
          <th>
            <i18n path="common.tbTitle.args"></i18n>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>values</td>
          <td>
            <i18n path="region.event.values"></i18n>
          </td>
          <td>
            <pre class="brush:js">
          //argument value for "select", "column" and "group" mode
          {
            province: { key:'', value:'' },
            city: { key:'', value:'' },
            area: { key:'', value:'' },
            town: { key:'', value:'' }
          }
          //city picker:
          [
            { key: '', value: '' },
            { key: '', value: '' },
            ...
          ]
          </pre>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      selected: {
        province: '350000',
        city: '350100',
        area: '350104',
        town: '350104008'
      },
      selectedCity: ['110000', '130200', '140100'],
      selectResult: {},
      selectedCityResult: []
    }
  },
  methods: {
    regionChange (d) {
      console.log(d)
    },
    getValue (obj) {
      return obj ? obj.value : ''
    },
    selectLogs (data) {
      if (data) this.selectResult = data
    },
    citySelectedLogs (datas) {
      if (datas && datas.length) this.selectedCityResult = datas
    },
    resultText (region) {
      if (!region || !Object.values(region).some(val => val)) return 'null'
      return Object.values(region).filter(val => val).map(val => val.value).join(',')
    }
  },
  mounted () {
    window.SyntaxHighlighter.highlight()
  }
}
</script>
